/* -------------------------------------------------------------------------- */
/*                                   contact                                   */
/* -------------------------------------------------------------------------- */
.contact {
  position: relative;
  width: 100vw;
  height: 110vh;
  font-family: $openSans;
  background-color: $skyBlue;
  background-color: $darkBlue;
  color: $White;
  overflow: hidden;
  &-container {
    // padding-top: 80px;
    width: 80%;
    margin: auto;

    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;

    input[type="text"],
    input[type="email"],
    textarea {
      color: $white2;
      border: none;
      outline: none;
      border-bottom: 1px solid $white2;
      background-color: transparent;
      padding: 10px;
      font-size: 1rem;
      display: block;
      width: 100%;
      margin-bottom: 20px;
      resize: none;
    }
    textarea {
      border: 1px solid $white2;
    }
    form {
      width: 50%;
      text-align: center;
    }
    &-content {
      width: 50%;
      padding: 40px;

      &-details {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        font-size: 1.2rem;
        font-weight: 400;
        font-family: $mont;
        img {
          margin-right: 20px;
          height: 30px;
          width: 30px;
        }
      }
      a:hover {
        text-decoration: underline;
      }
    }

    .submit {
      background-color: transparent;
      padding: 10px 25px;
      color: $white2;
      border: 1px solid $white2;
      cursor: pointer;
      font-size: 1.2rem;
      font-weight: 500;
      &:hover {
        background-color: $white2;
        color: $darkBlue;
      }
    }

    &-wrapper {
      display: flex;
      align-items: center;
      margin-bottom: 30px;
    }
    &-title {
      width: 50%;
      padding: 20px;
      font-size: 4rem;
      font-weight: 600;
      text-transform: capitalize;
      letter-spacing: 3px;
      font-family: $mont;
    }
    hr {
      width: 2px;
      height: 100%;
      background: white;
      border: none;
    }
  }

  .gratitude {
    text-align: center;
    font-size: 2rem;
    margin-top: 40px;
    color: $white2;
  }
}

.footer {
  position: absolute;
  bottom: 0;
  font-size: 1.2rem;
  font-family: $openSans;
  color: $white2;
  width: 100%;
  padding: 10px;
  background: rgba(18, 98, 153, 0.15);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &-container {
    display: flex;
    align-items: center;
    .icon-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      border: 1px solid $white2;
      margin: 0 20px;
      background: $white2;
      height: 40px;
      width: 40px;
    }
    img {
      height: 20px;
      cursor: pointer;
    }
  }
}

@media only screen and (max-width: 1024px) {
  .contact {
    &-container {
      width: 100%;
      &-title {
        font-size: 2.5rem;
      }
    }
  }
}
@media only screen and (max-width: 768px) {
  .contact {
    &-container {
      &-wrapper {
        flex-direction: column;
      }
      &-title {
        width: 100%;
        text-align: center;
      }
      &-content {
        width: 100%;
        // display: flex;
        // flex-direction: column;
        &-details {
          justify-content: center;
          // width: 60%;
          text-align: left;
        }
      }
    }
  }
}

@media only screen and (max-width: 486px) {
  .contact {
    form {
      width: 90%;
    }
    .gratitude {
      font-size: 1.2rem;
    }
    &-container {
      justify-content: flex-start;
      margin: 100px 0 20px 0px;
      &-title {
        font-size: 2rem;
      }
      &-wrapper {
        justify-content: center;
      }
      &-content {
        width: 82%;
        padding: 10px;
        &-details {
          font-size: 1rem;
          justify-content: flex-start;
        }
      }
    }
  }
  .footer {
    .icon-wrapper {
      margin: 0 10px;
    }
  }
}
