@import "./variables";
@import "./navbar";
@import "./introduction";
@import "./about";
@import "./projects";
@import "./skills";
@import "./common";
@import "./contact";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app__container {
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    background: #252525;
  }
  &::-webkit-scrollbar-thumb {
    background: $theme;
    border-radius: 30px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: $theme;
  }
}

.section-title {
  font-size: 3rem;
  font-weight: 500;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  color: $White;
  .square {
    width: 10px;
    height: 50px;
    background: $skyBlue;
    margin-right: 15px;
  }
}

.fallbackLoader {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $theme;
}
.gap-between-pages {
  height: 10vh;
  background-color: $theme;
}
a {
  text-decoration: none;
  color: inherit;
}

@media only screen and (max-width: 768px) {
  .section-title {
    font-size: 2.5rem;
  }
}

@media only screen and (max-width: 486px) {
  .section-title {
    font-size: 1.5rem;
    .square {
      height: 25px;
      width: 5px;
      margin-right: 10px;
    }
  }
}
